<template>
  <div class="home">
    <!-- 二级路由插座 -->
    <router-view></router-view>


    <!-- 底部 -->
    <van-tabbar  v-show="$store.state.vanTabbar">
      <van-tabbar-item
        :name="item.name"
        :icon="item.icon"
        v-for="(item,index) in tabbarData"
        :key="index"
        @click.native="goPage(item)"
      >{{item.text}}</van-tabbar-item>
    </van-tabbar>
  </div>
  
</template>

<script>
export default {
  data: function() {
    return {
      tabbarName: "classlist"
    };
  },
  computed: {
    tabbarData() {
      return this.$store.state.mainModule.tabbarData;
    }
  },
  methods: {
    // 点击跳转页面
    goPage(item) {
      this.$router.push({ name: item.routeName });
    },


  }
};
</script>

<style lang="scss" scoped>
</style>